<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .filter {
        margin-top: 20px;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }

    .num-input {
        width: 100px;
    }

</style>

<template>
    <div class="page">
        <div>
            <el-row class="header">

                <el-col class="filter-label" :span="2">规则编号:</el-col>

                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.id" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2">状态:</el-col>

                <el-col :span="4">
                    <el-select class="filter-match" v-model="filter.status" placeholder="请选择">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>

                <el-col class="filter-label" :span="2">真实名称:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.real_name" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-btn" :span="6">
                    <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                    <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                    <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
                </el-col>
            </el-row>

            <el-row style="margin-top: 20px" class="filter">
                <el-col class="filter-label" :span="2">注册时间:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="filter.register_time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>
                <el-col class="filter-label" :span="2">登录时间:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="filter.login_time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>
                <el-col class="filter-label" :span="2">注册IP:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.create_ip"
                              placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2">最后登录IP:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.login_ip"
                              placeholder="请输入"></el-input>
                </el-col>
            </el-row>
        </div>
        <el-table
                :data="data.data"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="id"
                    label="角色ID">
            </el-table-column>
            <el-table-column
                    prop="mobile"
                    label="账号">
            </el-table-column>
            <el-table-column
                    prop="real_name"
                    label="真实姓名">
            </el-table-column>
            <el-table-column
                    prop="role"
                    label="角色">
                <template slot-scope="scope">
                    <span v-for="(item ,index) in scope.row.role" v-bind:key="item.id">{{item.name}} <span
                            v-show="index!==scope.row.role.length-1">,</span></span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="login_time"
                    label="最后登录时间">
            </el-table-column>
            <el-table-column
                    prop="status"
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.status===1?'启用':'未启用'}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="detail(scope.row)" type="text" size="small">详细</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :page-count="data.last_page"
                    @current-change="loadData">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import api from '../../js/api/admin'

    export default {
        name: "User",
        data() {
            return {
                options: [
                    {
                        value: '1',
                        label: '正常'
                    },
                    {
                        value: '0',
                        label: '禁用'
                    }
                ],
                filter: {
                    id: null,
                    status: null,
                    real_name: null,
                    register_time: null,
                    register_start: null,
                    register_end: null,
                    login_time: null,
                    login_start: null,
                    login_end: null,
                    login_ip: null,
                    create_ip: null,
                    jump_page: null,
                },
                data: {}
            }
        },
        mounted() {
            this.loadData(1);
        },
        methods: {
            loadData(page) {
                this.filter.jump_page = page;

                if (this.filter.register_time != null && this.filter.register_time.length === 2) {
                    this.filter.register_start = this.filter.register_time[0];
                    this.filter.register_end = this.filter.register_time[1]
                } else {
                    this.filter.register_start = null;
                    this.filter.register_end = null
                }
                if (this.filter.login_time != null && this.filter.login_time.length === 2) {
                    this.filter.login_start = this.filter.login_time[0];
                    this.filter.login_end = this.filter.login_time[1]
                } else {
                    this.filter.login_start = null;
                    this.filter.login_end = null
                }
                api.adminList(this.filter, res => {
                    this.data = res.data.data;
                })
            },
            ref() {
                this.filter = {
                    id: null,
                    status: null,
                    real_name: null,
                    register_time: null,
                    register_start: null,
                    register_end: null,
                    login_time: null,
                    login_start: null,
                    login_end: null,
                    login_ip: null,
                    create_ip: null,
                    jump_page: null,
                };
                this.loadData(1);

            },
            filterOpen() {
                this.filter.isOpen = !this.filter.isOpen;
            },
            add() {
                this.$router.push({
                    name: "settingUserAdd",
                });
            },
            detail(item) {
                this.$router.push({
                    name: "settingUserAdd",
                    query: {
                        id: item.id,
                    },
                });
            },
        }
    }
</script>

